[class$="-list"]{list-style:none;padding:0;margin:0}.chat-container{width:100%;height:100%;overflow:hidden;display:flex}.chat-side{width:200px;background-color:#002140;display:flex;align-items:center;flex-direction:column;padding:16px 8px;position:relative;transition-duration:0.3s;z-index:1}.chat-side.is-open{margin-left:0px}.chat-side:not(.is-open){margin-left:-200px}.chat-main{flex:1;background-color:#e6f4ff;padding:8px 16px;position:relative}.side-collapse{position:absolute;right:0;top:50%;transition-duration:0.3s;z-index:1}.side-collapse.is-open{transform:translate(50%, -50%)}.side-collapse:not(.is-open){transform:translate(80%, -50%)}.side-new{width:100%;margin-bottom:16px}.side-list{width:100%;display:flex;flex-direction:column;gap:16px}.side-item{width:100%;color:#ffffff;font-size:1.2em;border:1px solid #fff;border-radius:8px;padding:8px 4px 8px 16px;display:flex;justify-content:space-between;cursor:pointer}.side-item-title{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.side-item-button{width:55px;min-width:55px}.side-item.active,.side-item:hover{background-color:#3f83b7}.main-list{position:absolute;top:0;left:0;height:calc(100% - 110px);width:100%;overflow-y:auto;padding-top:8px;display:flex;flex-direction:column}.main-item{width:100%;display:flex;padding:8px 16px;gap:8px}.main-item:nth-child(even){background-color:#cae1f4}.main-icon,.main-text{font-size:1.8em;padding:8px}.main-empty{width:fit-content;left:50%;top:30%;position:absolute;transform:translate(-50%, 0);text-align:center}.main-submit{position:absolute;bottom:0;left:0;width:100%;height:140px;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:0px 16px 8px 16px;background-image:linear-gradient(180deg, rgba(53,55,64,0), #e6f4ff 50%)}.main-wait{align-self:flex-start}
